import { useNavigate, useParams } from "react-router"
import { getCategoryAPI, updateCategoryAPI } from "../../service/category.service"
import { useEffect, useState } from "react"
import { useSelector } from "react-redux"

function UpdateCategory() {
    let { id } = useParams()
    console.log(id)
    let [category, setCategory] = useState({ id, name: "" })

    let { categories } = useSelector(store => store.category)

    let navigate = new useNavigate()

    let getData = async () => {
        let body = categories.find(item => item.id === parseInt(id));
        setCategory(body)
    }

    useEffect(() => { getData() }, [id])

    let handleChangeName = (e) => {
        setCategory({ ...category, name: e.target.value })
    }
    let handleSubmit = async () => {
        try {
            let data = await updateCategoryAPI(category)
            console.log(data)
            navigate("/categories")
        } catch (e) {
            console.log(e)
        }
    }
    return (
        <div>
            <label>ID:</label>
            <input value={id} readOnly />
            <label>Name:</label>
            <input value={category.name} onChange={handleChangeName} />
            <button onClick={handleSubmit}>Save</button>
        </div >
    )
}

export default UpdateCategory
