import { Link } from "react-router-dom";
import { ThemeContext } from "../../context/ThemeContext";
import { useContext } from "react";
import { AuthContext } from "../../context/AuthContext";
import { useMe } from "../hook/useAuth";
import { useDispatch, useSelector } from "react-redux";
import { incNumber } from "../redux/counterSlice";

function Menu() {
    let { mode, setMode } = useContext(ThemeContext)
    let { setAuth, user } = useContext(AuthContext)

    // co the xai ham hook
    const u = useMe()

    const counter = useSelector((store) => store.counter)
    const dispatch = useDispatch();

    let handleMode = (mode) => {
        setMode(mode === 'light' ? 'dark' : 'light')
        //dispatch(increment()); // goi action type increament
        dispatch(incNumber(10))
    }

    let handleLogout = () => {
        setAuth(false)
        localStorage.removeItem("token")
    }
    return (
        <ul>
            <li>
                Xin chao {u.name}-{counter.x}
            </li>
            <li>
                <button onClick={() => handleMode(mode)}>{mode}</button>
            </li>
            <li>
                <Link to="/home">Home</Link>
            </li>
            <li>
                <Link to="/categories">Category</Link>
            </li>
            <li>
                <Link to="/user">User</Link>
            </li>
            <li>
                <Link to="/post">Post</Link>
            </li>
            <li>
                <Link to="/test">Test Page</Link>
            </li>
            <li>
                <button onClick={handleLogout}>Logout</button>
            </li>
        </ul>
    )
}

export default Menu