import { axiosJmaster } from "../util/axios";

export function searchCategoryAPI(search) {

    let data = JSON.stringify(search);

    let config = {
        method: 'post',
        maxBodyLength: Infinity,
        url: '/category/search',
        headers: {
            'Content-Type': 'application/json'
        },
        data: data
    };

    return axiosJmaster.request(config)
        .then((response) => {
            let body = response.data // du lieu tra ve 
            return body
        })
        .catch((error) => {
            console.log(error)
        });

}
export let addCategoryAPI = (category) => {
    let data = JSON.stringify(category);

    let config = {
        method: 'post',
        maxBodyLength: Infinity,
        url: '/admin/category/add',
        headers: {
            'Authorization': 'Bearer ' + localStorage.getItem("token"),
            'Content-Type': 'application/json',
        },
        data: data
    };

    return axiosJmaster.request(config)
        .then((response) => {
            console.log(JSON.stringify(response.data))
            return response.data
        })
        .catch((e) => {
            console.log(e);
        });

}

export const deleteCategoryAPI = (id) => {
    let config = {
        method: 'delete',
        maxBodyLength: Infinity,
        url: '/admin/category/delete?id=' + id,
        headers: {
            'Authorization': 'Bearer ' + localStorage.getItem("token"),
        },
    };

    return axiosJmaster.request(config)
        .then((response) => {
            return response.data
        })
        .catch((error) => {
            console.log(error);
        });
}
