
import { useEffect, useState } from "react"
import { useNavigate } from "react-router"
import { searchCategoryAPI } from "../../service/category.service"
import { addPostAPI } from "../../service/post.service"
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import Button from '@mui/material/Button';


function AddPost() {
    let [post, setPost] = useState({ title: "", description: "", categoryId: "", imageFile: null })

    let [categories, setCategories] = useState([])
    let [search, setSearch] = useState({
        "start": 0,
        "length": 20,
        "search": {
            "value": ""
        }

    })

    useEffect(() => {
        fetchData()
    }, [search])

    const fetchData = async () => {
        try {
            let body = await searchCategoryAPI(search)
            setCategories(body.data)
        } catch (e) {
            console.log(e)
        }
    }

    let navigate = new useNavigate()

    let handleChangeText = (e) => {
        setPost({ ...post, [e.target.name]: e.target.value })
    }

    let handleChangeFile = (e) => {
        setPost({ ...post, imageFile: e.target.files[0] })
    }
    let handleSubmit = async () => {
        let data = new FormData();
        data.append("title", post.title)
        data.append("description", post.description)
        data.append("categoryId", post.categoryId)
        data.append("imageFile", post.imageFile)

        await addPostAPI(data)
        navigate("/post")


    }
    return (
        <div>
            <h1>New Post</h1>
            <div>
                <label>Title:</label>
                <input name="title" onChange={handleChangeText} />
                <br />
                <div>
                    <label>Description:</label>
                    {/* <input name="description" onChange={handleChangeText} /> */}
                    <ReactQuill style={{ height: 300 }} theme="snow" value={post.description}
                        onChange={(text) => setPost({ ...post, description: text })}
                    />
                </div>
                <div style={{ marginTop: 50 }}>
                    <label>CategoryId:</label>
                    <select name="categoryId" onChange={handleChangeText} >
                        {categories.map(c => {
                            return (
                                <option key={c.id} value={c.id}>{c.name}</option>
                            )
                        })}
                    </select>
                </div>
                <br />
                <label>Image File::</label>
                <input type="file" accept="image/*" onChange={handleChangeFile} />
                <br />
                {/* <button onClick={handleSubmit}>Save</button> */}
                <div>
                    <Button variant="contained" onClick={handleSubmit}>Save</Button>
                </div>
            </div>
        </div >
    )
}

export default AddPost