
import { useEffect, useState } from "react"
import { Link, useNavigate } from "react-router-dom"
import { deletePostAPI, searchPostAPI } from "../../service/post.service";

function ListPost() {
    let navigate = useNavigate()

    let [posts, setPosts] = useState([])
    let [search, setSearch] = useState({
        "start": 0,
        "length": 10,
        "search": {
            "value": ""
        }
    })
    let [totalPage, setTotalPage] = useState(0)

    useEffect(() => {
        fetchData()
    }, [search])

    const fetchData = async () => {
        try {
            let body = await searchPostAPI(search)
            console.log(body) // thuoc tinh data, xem postman
            console.log(body.recordsFiltered) // thuoc tinh data, xem postman
            setPosts(body.data)
            // tinh tong so trang 
            let totalPage = Math.ceil(body.recordsFiltered / search.length)
            setTotalPage(totalPage)
        } catch (e) {
            console.log(e)
        }

    }

    const handleDelete = async (id) => {
        try {
            await deletePostAPI(id)
            fetchData()
        } catch (e) {
            console.log(e)
        }
    }

    const handleSearch = (e) => {
        setSearch({ ...search, "search": { "value": e.target.value } })
    }

    const handleNext = () => {
        setSearch({ ...search, "start": search.start + search.length })
    }
    const handlePrev = () => {
        setSearch({ ...search, "start": search.start > 0 ? search.start - search.length : 0 })
    }
    const handleChangeSize = (e) => {
        const size = e.target.value
        setSearch({ ...search, "length": parseInt(size) })
    }
    const handlePage = (index) => {
        setSearch({ ...search, "start": search.length * index })
    }
    return (
        <div>
            <Link to="/post/create">Create Post</Link>
            <div>
                <input onChange={handleSearch}></input>
            </div>
            <table border={1}>
                <thead>
                    <tr>
                        <th>ID</th>
                        <th>Title</th>
                        <th>Description</th>
                        <th>Image</th>
                        <th>Category</th>
                        <th>Create By</th>
                        <th>Create At</th>
                        <th>Action</th>
                    </tr>
                </thead>
                <tbody>
                    {posts.map(c => {
                        return (
                            <tr key={c.id}>
                                <td>{c.id}</td>
                                <td>{c.title}</td>
                                <td>{c.description}</td>
                                <td>
                                    {c.images?.map(image => (
                                        <img src={`http://52.193.212.182:8080/image/${image}`} alt="" width={100} key={image} />
                                    ))}
                                </td>
                                <td>{c.categoryName}</td>
                                <td>{c.createBy}</td>
                                <td>{c.createdDate}</td>

                                <td><button onClick={() => handleDelete(c.id)}>Delete</button> | <button onClick={() => navigate(`/post/update/${c.id}`)} >Edit</button></td>
                            </tr>
                        )
                    })}
                </tbody>
            </table>
            <select onChange={handleChangeSize}>
                <option value={5}>5</option>
                <option value={10}>10</option>
                <option value={15}>15</option>
            </select>
            <button onClick={handlePrev}>Prev</button>
            {
                Array.apply(null, { length: totalPage }).map((_, index) => {
                    return (
                        <button onClick={() => handlePage(index)}>{index}</button>
                    )
                })

            }
            <button onClick={handleNext}>Next</button>
            <p>Tng s trang : {totalPage}</p>

        </div >
    )
}

export default ListPost