import { useNavigate, useParams } from "react-router"
import { getCategoryAPI, searchCategoryAPI, updateCategoryAPI } from "../../service/category.service"
import { useEffect, useState } from "react"
import { getPostAPI, updatePostAPI } from "../../service/post.service"

function UpdatePost() {
    let { id } = useParams()
    console.log(id)
    let [post, setPost] = useState({ id: "", title: "", description: "", categoryId: "", imageFile: null })

    let navigate = new useNavigate()

    let getData = async () => {
        let body = await getPostAPI(id)
        setPost(body)
    }

    useEffect(() => { getData() }, [id])

    let [categories, setCategories] = useState([])
    let [search, setSearch] = useState({
        "start": 0,
        "length": 20,
        "search": {
            "value": ""
        }

    })

    useEffect(() => {
        fetchData()
    }, [search])

    const fetchData = async () => {
        try {
            let body = await searchCategoryAPI(search)
            setCategories(body.data)
        } catch (e) {
            console.log(e)
        }
    }
    let handleSubmit = async () => {

        let data = new FormData();
        data.append("id", post.id)
        data.append("title", post.title)
        data.append("description", post.description)
        data.append("categoryId", post.categoryId)
        data.append("imageFile", post.imageFile)

        await updatePostAPI(data)
        console.log(data)
        navigate("/post")


    }

    let handleChangeText = (e) => {
        setPost({ ...post, [e.target.name]: e.target.value })
    }
    let handleChangeFile = (e) => {
        setPost({ ...post, imageFile: e.target.files[0] })
    }
    return (
        <div>
            <h1>Edit Post</h1>
            <div>
                <label>ID:</label>
                <input name="id" value={post.id} onChange={handleChangeText} readOnly />
                <br />
                <label>Title:</label>
                <input name="title" value={post.title} onChange={handleChangeText} />
                <br />
                <label>Description:</label>
                <input name="description" value={post.description} onChange={handleChangeText} />
                <br />
                <label>CategoryId:</label>
                <select name="categoryId" onChange={handleChangeText} >
                    {categories.map(c => {
                        return (
                            <option key={c.id} value={c.id}>{c.name}</option>
                        )
                    })}
                </select>
                <br />
                <label>Image File::</label>
                <input type="file" accept="image/*" onChange={handleChangeFile} />
                <br />
                <button onClick={handleSubmit}>Save</button>
            </div>
        </div>
    )
}

export default UpdatePost
