import { axiosJmaster } from "../util/axios";

export const meAPI = () => {

    let config = {
        method: 'get',
        maxBodyLength: Infinity,
        url: '/member/me',
        headers: {
            'Authorization': 'Bearer ' + localStorage.getItem("token"),
        },
    };

    return axiosJmaster.request(config)
        .then((response) => {
            console.log(JSON.stringify(response.data));
            return response.data
        })
}