
import { useContext, useEffect, useState } from "react"
import { Link, useNavigate } from "react-router-dom"
import { deleteCategoryAPI, searchCategoryAPI } from "../../service/category.service";
import { ThemeContext } from "../../context/ThemeContext";
import { Pagination } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { store } from "../../component/redux/store";
import { searchThunk } from "../../component/redux/categorySlice";
import { toast } from "react-toastify";

function ListCategory() {
    let navigate = useNavigate()

    const dispatch = useDispatch();

    let { categories,
        recordsFiltered,
        totalPage } = useSelector((store) => store.category)

    // let [categories, setCategories] = useState([])
    let [search, setSearch] = useState({
        "start": 0,
        "length": 5,
        "search": {
            "value": ""
        }

    })
    // let [totalPage, setTotalPage] = useState(0)

    useEffect(() => {
        fetchData()
    }, [search])

    const fetchData = async () => {
        dispatch(searchThunk(search))
    }

    const handleDelete = async (id) => {
        try {
            await deleteCategoryAPI(id)
            fetchData()
            toast("Xoa thanh cong danh muc", { type: 'success' })
        } catch (e) {
            console.log(e)
        }
    }

    const handleSearch = (e) => {
        setSearch({ ...search, "search": { "value": e.target.value } })
    }

    const handleNext = () => {
        setSearch({ ...search, "start": search.start + search.length })
    }
    const handlePrev = () => {
        setSearch({ ...search, "start": search.start > 0 ? search.start - search.length : 0 })
    }
    const handleChangeSize = (e) => {
        const size = e.target.value
        setSearch({ ...search, "length": parseInt(size) })
    }
    const handlePage = (index) => {
        setSearch({ ...search, "start": search.length * index })
    }
    return (
        <div>
            <Link to="/category/create">Create Category</Link>
            <div>
                <input onChange={handleSearch}></input>
            </div>
            <table border={1}>
                <thead>
                    <tr>
                        <th>ID</th>
                        <th>Name</th>
                        <th>Action</th>
                    </tr>
                </thead>
                <tbody>
                    {categories.map(c => {
                        return (
                            <tr key={c.id}>
                                <td>{c.id}</td>
                                <td>{c.name}</td>
                                <td><button onClick={() => handleDelete(c.id)}>Delete</button> | <button onClick={() => navigate(`/category/update/${c.id}`)} >Edit</button></td>
                            </tr>
                        )
                    })}
                </tbody>
            </table>
            <select onChange={handleChangeSize}>
                <option value={5}>5</option>
                <option value={10}>10</option>
                <option value={15}>15</option>
            </select>
            {/* <button onClick={handlePrev}>Prev</button>
            {
                Array.apply(null, { length: totalPage }).map((_, index) => {
                    return (
                        <button onClick={() => handlePage(index)}>{index}</button>
                    )
                })

            }
            <button onClick={handleNext}>Next</button>
            <p>Tng s trang : {totalPage}</p> */}
            <Pagination count={totalPage} variant="outlined" color="primary" showFirstButton showLastButton onChange={(e, value) => handlePage(value - 1)} />

        </div >
    )
}

export default ListCategory