import { axiosJmaster } from '../util/axios';

export let addPostAPI = (data) => {

    let config = {
        method: 'post',
        maxBodyLength: Infinity,
        url: '/member/post/add',
        headers: {
            'Authorization': 'Bearer ' + localStorage.getItem("token"),
        },
        data: data
    };

    return axiosJmaster.request(config)
        .then((response) => {
            console.log(JSON.stringify(response.data));
            return response.data
        })
        .catch((error) => {
            console.log(error);
        });
}

export function searchPostAPI(search) {

    let data = JSON.stringify(search);

    let config = {
        method: 'post',
        maxBodyLength: Infinity,
        url: '/post/search',
        headers: {
            'Content-Type': 'application/json'
        },
        data: data
    };

    return axiosJmaster.request(config)
        .then((response) => {
            let body = response.data // du lieu tra ve 
            return body
        })
        .catch((error) => {
            console.log(error)
        });

}
export const getPostAPI = (id) => {
    let config = {
        method: 'get',
        maxBodyLength: Infinity,
        url: '/post/' + id,
    };

    return axiosJmaster.request(config)
        .then((response) => {
            console.log(JSON.stringify(response.data));
            return response.data
        })
        .catch((e) => {
            console.log(e);
        });
}

export const deletePostAPI = (id) => {
    let config = {
        method: 'delete',
        maxBodyLength: Infinity,
        url: '/admin/post/delete?id=' + id,
        headers: {
            'Authorization': 'Bearer ' + localStorage.getItem("token"),
        },
    };

    return axiosJmaster.request(config)
        .then((response) => {
            return response.data
        })
        .catch((error) => {
            console.log(error);
        });
}
